/* global WuLai */
import importScript from '~global-js/helpers/import-script';
import { getAuthToken, decodeUserInfo } from '~global-js/helpers/auth';
import cookies from 'js-cookie';
import fireCustomEvent from '~global-js/helpers/custom-events';
import customEvents from '~global/variables/custom-events.json';
import { getLocale } from '~global-js/helpers/locale';
import { isLargeScreen } from '~global-js/helpers/breakpoints';
import { event } from '~global-js/helpers/tracking';

export default class Laiye {

  constructor() {
    this.wulai = null;
  }

  init = () => {
    if (window.hurricane && window.hurricane.i18nInitialised) {
      this.loadScript(this.callback);
    } else {
      document.addEventListener(customEvents.i18nInitialised.name, () => {
        this.loadScript(this.callback);
      });
    }
  }

  callback = () => {
    this.configWulai();
    fireCustomEvent(customEvents.wulaiReady.name);
  }

  fireEvent = () => {
    // Tracking
    event({
      c: 'enquiry',
      a: 'success',
      l: 'laiye',
      mp: [{
        type: 'track',
        key: 'Enquiry Success',
        params: {
          'Auth Source': 'Live Chat',
          'Auth Method': 'Unknown',
          'Auth Type': 'Login',
          'Enquiry Type': 'Laiye',
        },
      }],
    });
  }

  configWulai = () => {
    this.wulai = new WuLai({
      autoOpen: false,
      fullScreen: false,
      minimize: 0,
      userId: this.getUserId(),
      userInfo: this.fetchUserInfo(),
      onSubmit: this.fireEvent,
    });
    this.wulai.setServiceName('Student.com');
  }

  open = () => {
    this.wulai.open();
  }

  getUserId = () => {
    let userId;
    if (!getAuthToken()) {
      userId = cookies.get('uid');
    } else {
      userId = decodeUserInfo(getAuthToken()).uuid;
    }
    return userId;
  }

  fetchUserInfo = () => {
    const userInfo = {
      campaign: {
        gclid: cookies.get('gclid'),
        utm_source: cookies.get('utm_source'),
        utm_medium: cookies.get('utm_medium'),
        utm_campaign: cookies.get('utm_campaign'),
        utm_term: cookies.get('utm_term'),
        utm_content: cookies.get('utm_content'),
      },
      source: getLocale() === 'zh-cn' ? 'cn' : 'www',
      source_device: isLargeScreen() ? 'desktop' : 'mobile',
      source_details: 'wulaibot_chattool',
      // New added fields but it's not used yet.
      // property: 276,
      // wechat: 'wechat_id',
      // language: 'zh-cn',
    };
    return userInfo;
  }

  loadScript(callback) {
    if (this.loaded && callback) {
      callback();
    } else if (window.hurricane.staticHash) {
      this.importWulaiScript(callback);
    } else {
      document.addEventListener(customEvents.staticHashInitialised.name, () => {
        this.importWulaiScript(callback);
      });
    }
  }

  importWulaiScript(callback) {
    const WULAI_SCRIPT = `https://xuelvjiasdk.wul.ai/static/build/wulai-${window.hurricane.staticHash.wulai}.js`;
    importScript(WULAI_SCRIPT).then(() => {
      this.loaded = true;
      if (callback) {
        callback();
      }
    });
  }
}



