/* global Intercom */
import { getAuthToken, decodeUserInfo, getUserInfo } from '~global-js/helpers/auth';
import importScript from '~global-js/helpers/import-script';
import axios from '~global-js/helpers/axios';
import generatePath from '~global-js/helpers/router';
import fireCustomEvent from '~global-js/helpers/custom-events';
import customEvents from '~global/variables/custom-events.json';
import { getLocale } from '~global-js/helpers/locale';
import cookies from 'js-cookie';

export default class IntercomClass {
  constructor() {
    this.closeStatus = [
      'cancellation_processing',
      'cancelled',
      'booking_confirmed',
      'cancellation_processing',
    ];
    this.config = {};
    this.openRecord = null;
    this.openLead = null;
    this.openOpportunity = null;
    this.enquirySuccess = false;
    this.initedChat = false;
    this.locale = getLocale();
    this.readyEvent = null;
    this.seaCountryCodes = [
      '62', // Indonesia
      '66', // Thailand
      '84', // Vietnam
      '65', // Singapore
      '60', // Malaysia
    ];
    this.ausCountryCode = '61';
  }

  init(flowType = 'record') {
    if (flowType === 'record') {
      this.initChat();
    }

    // logout event
    document.addEventListener(customEvents.userLogout.name, () => {
      cookies.remove('intercom-live-chat-showed', {
        path: '/',
      });

      if (this.initedChat && Intercom) { // eslint-disable-line new-cap
        Intercom('shutdown'); // eslint-disable-line new-cap
      }
    });
  }

  initChat() {
    if (this.initedChat) {
      return;
    }

    const token = getAuthToken();
    this.initedChat = true;
    const arrLocale = getLocale().split('-');
    const newLocale = [arrLocale[0], arrLocale[1].toUpperCase()].join('_');

    if (typeof token !== 'undefined') {
      document.addEventListener(customEvents.receivedUser.name, () => {
        const tokenInfo = decodeUserInfo(token);
        const userInfo = getUserInfo();

        axios.all([this.getUserRecords(tokenInfo), this.getCurrentJourneys(tokenInfo)]).then(() => {
          let isShanghaiOffice = false;
          const exp = new RegExp(`[(+\\s]+${this.ausCountryCode}`);

          if (this.openRecord && userInfo.phone_number.match(exp) && this.openRecord.country_slug === 'au') {
            isShanghaiOffice = true;
          }

          if (isShanghaiOffice) {
            window.intercomSettings = {
              app_id: 'xfgddsl7',
              name: [userInfo.first_name, userInfo.last_name].join(' '),
              email: userInfo.email,
              mobile: userInfo.phone_number,
              opportunity_number: this.openOpportunity ? this.openOpportunity.reference_id : null,
              opportunity_link: this.openOpportunity ? this.openOpportunity.internal_link : null,
              record_status: this.openRecord ? this.openRecord.status : null,
              record_destination_city: this.openRecord ? this.openRecord.city_name : null,
              record_destination_country: this.openRecord ? this.openRecord.country_slug : null,
              lead_number: this.openLead ? this.openLead.reference_id : null,
              lead_link: this.openLead ? this.openLead.internal_link : null,
              website_language: newLocale,
              show_welcome_message: true,
              intercom_type: 'Live Chat',
              opportunity_assignee: '',
              user_hash: this.config.user_hash,
              enquiry_in_working_hour: isShanghaiOffice ?
                  this.config.enquiry_in_working_hour_sha : this.config.enquiry_in_working_hour_uk,
              is_shanghai_office: isShanghaiOffice,
              user_id: tokenInfo.uuid,
            };

            this.setupIntercom();
          }
        }).catch((err) => {

        });
      });
    } else {
      window.intercomSettings = {
        app_id: 'xfgddsl7',
        website_language: newLocale,
        show_welcome_message: true,
        intercom_type: 'Live Chat',
      };
      this.setupIntercom();
    }
  }

  setupIntercom() {
    importScript(`https://widget.intercom.io/widget/${window.intercomSettings.app_id}`)
      .then(() => {
        const cookieIntercomLiveChat = cookies.get('intercom-live-chat-showed');
        if ((typeof cookieIntercomLiveChat === 'undefined' || !cookieIntercomLiveChat) && getLocale() === 'zh-cn') {
          Intercom('show'); // eslint-disable-line new-cap
          cookies.set('intercom-live-chat-showed', true, { path: '/' });
        }

        if (typeof this.readyEvent === 'function') {
          this.readyEvent();
        }

        fireCustomEvent(customEvents.intercomReady.name);
      });
  }

  getUserRecords(tokenInfo) {
    return axios
      .get(generatePath('get-intercom-user-records', { uuid: tokenInfo.uuid }), {})
      .then((response) => {
        this.openRecord = this.fetchOpenRecord(response.data);
        this.config.user_hash = response.data.user_hash;
        this.config.enquiry_in_working_hour_uk = response.data.enquiry_in_working_hour_uk;
        this.config.enquiry_in_working_hour_sha = response.data.enquiry_in_working_hour_sha;
        return response;
      });
  }

  getCurrentJourneys(tokenInfo) {
    return axios
        .get(generatePath('get-journeys-current', { uuid: tokenInfo.uuid }), {})
        .then((response) => {
          const currentJourney = response.data;
          if (currentJourney &&
              currentJourney.reference_id &&
              currentJourney.reference_id.match(/^O\d+$/)) {
            this.openOpportunity = response.data;
          } else if (currentJourney && currentJourney.reference_id &&
              currentJourney.reference_id.match(/^L\d+$/)) {
            this.openLead = response.data;
          }
          return response;
        });
  }

  fetchOpenRecord(userRecords) {
    let openRecord = null;
    if (!userRecords) {
      return openRecord;
    }
    for (const record of userRecords.records.records) {
      if (this.closeStatus.indexOf(record.status) === -1) {
        openRecord = record;
        break;
      }
    }
    return openRecord;
  }

  onReady(event) {
    this.readyEvent = event;
  }

  open() {
    if (this.initedChat && Intercom) { // eslint-disable-line new-cap
      Intercom('show'); // eslint-disable-line new-cap
    }
  }
}



